import React, {Component} from 'react';
import axios from 'axios';
import './filter.css';


const url = "https://developrestaurant.herokuapp.com/restaurantlist";
class CusineFilter extends Component {

    cuisineLogic = (event)=>{
        let mealtype = sessionStorage.getItem('type');
        let cuisineType= event.target.value;
        let curl;
        if(cuisineType===""){
            curl= `${url}/${mealtype}`
        }else{
            curl=  `${url}/${mealtype}?cuisine=${event.target.value}`
        }

        axios.get(curl)
        .then((response)=>{
            this.props.datapercuisine(response.data)});
        
    }

    render(){
        return(
            <div>
                 <div class="cuisine ">
                        <p class="cuisine_head">Cuisine</p>
                        <ul class="cuisine_list" onChange={this.cuisineLogic}>
                            <li>
                                <label className="radio">
                                <input type="radio" value="" name="cuisine"/> All
                                </label>
                            </li>
                            <li>   
                            <label className="radio">
                                <input type="radio" value="1" name="cuisine"/> North Indian
                                </label>
                            </li>
                            <li>   
                            <label className="radio">
                                <input type="radio" value="2" name="cuisine"/> South Indian
                                </label>
                            </li>
                            <li>    
                                <label className="radio">
                                <input type="radio" value="3" name="cuisine"/> Mongi Dish
                                </label>
                            </li>    
                            <li>
                            <label className="radio">
                                <input type="radio" value="4" name="cuisine"/> Fast Food
                                </label>
                            </li>   
                            <li>
                            <label className="radio">
                                <input type="radio" value="5" name="cuisine"/> Street Food
                                </label>
                            </li>   
                        </ul>
                    </div>
            </div>
        )               
    }
}
export default CusineFilter