import React,{Component} from 'react';
import './search.css';

const lurl = "https://developrestaurant.herokuapp.com/location"
const rurl= "https://developrestaurant.herokuapp.com/restaurant?city="

class Search extends Component{
	constructor(){
		super()
	this.state={
		location:'',
		restaurants:''
		}
	}	
		renderCity=(data)=>{
			if(data){
				return data.map((item) =>{
					return(
						<option value={item.city}>
							{item.city_name}
						</option>
						)
				})
			}
		}   
		renderRestaurants=(data)=>{
			if(data){
				return data.map((item) =>{
					return(
						<option value={item._id}>
							{item.name}|{item.locality}
						</option>
						)
				})
			}
		} 

		handleCity=(event)=>{
			console.log(event.target.value);
			fetch(`${rurl}${event.target.value}`,{method:'GET'})
			.then((res)=>res.json())
			.then((data)=>{
				this.setState({restaurants:data})
			})

		}

		handleRestaurant=(event)=>{
			this.props.restauid(Number(event.target.value))
		}
// passing value to other file

	render(){
		return(
		<div class="container px-0">
			<div class="row ">
				<div class="px-0 col-12   imagecontainer">
					<img src="https://i.postimg.cc/76kStDSv/food-wallpaper.png" alt="" />
					<span class="logo">
		                <b>R!</b>
		        	</span>	
					<div class="heading m-0">
		    		    <p>Find the Best Restaurant, cafes, bars</p>
		    		</div>
		    		<div className="locationSelector">
		    			<select className="location1" required onChange={this.handleCity}>
		                	<option>Select The City</option>
		                    {this.renderCity(this.state.location)}
		                </select>
						<select className="restaurantinput" required onChange={this.handleRestaurant}>
		                	<option>Select The Restaurant</option>
		                    {this.renderRestaurants(this.state.restaurants)}
		                </select>
		    		</div>
				</div>
			</div>
		</div>	
		)
		}

	componentDidMount(){
		fetch(lurl,{method:'GET'})
		.then((res)=>res.json())
		.then((data)=>{
			this.setState({location:data})
		})
	}
	}
	
export default Search