import React, {Component} from 'react';
import axios from 'axios';
import './filter.css';

const url = "https://developrestaurant.herokuapp.com/restaurantlist";

class CostFilter extends Component {
    
    filterLogic=(event)=>{
        console.log("filter")
        let mealtype= sessionStorage.getItem('type');
        let cost =(event.target.value).split(',');
        let lcost=cost[0];
        let hcost=cost[1];
        let curl;
        // " "-blank  ""-nthng
        if(event.target.value==" "|| event.target.value==""){
            curl=`${url}/${mealtype}`
        }else{
            curl=`${url}/${mealtype}?lcost=${lcost}&hcost=${hcost}`
        }
        axios.get(curl)
        .then((response)=>{this.props.datapercost(response.data)})
        console.log(curl)
    }

    render(){
        return(
            <React.Fragment>
               <div class="Costfortwo mb-2">
                    <p class="cost">Cost For Two</p>
                    <div onChange={this.filterLogic}>
                        <ul class="cost_list">
                            <li>
                                <label className="radio">
                                    <input type="radio" value="" name="cost"/>All
                                </label> 
                            </li>
                            <li>
                                <label className="radio">
                                    <input type="radio" value="100,400" name="cost"/>100 - 400
                                </label> 
                            </li>
                            <li>
                                <label className="radio">
                                    <input type="radio" value="401,800" name="cost"/>401 - 800
                                </label> 
                            </li>
                            <li>
                                <label className="radio">
                                    <input type="radio" value="801,1200" name="cost"/>801 - 1200
                                </label> 
                            </li>
                            <li>
                                <label className="radio">
                                    <input type="radio" value="1201,2000" name="cost"/>1201 - 2000
                                </label> 
                            </li>
                          
                        </ul>
                    </div>
                </div>
            </React.Fragment>
        )               
    }
}


export default CostFilter