class ListingDisplay extends React.Component{
    constructor(){
        super()

        this.state={
            color:''
        }
    }
        
    handleStar=(a)=>{
    
    }

    render(){

      const renderRest=({restdata})=>{
            console.log("...........???.>>>");
                if(restdata){
                    if(restdata.length>0){
                        return restdata.map((item)=>{
                            return(
        
                        <div className="item" id={item.id}>
                            <div class="contentmain px-0 mb-4">    
                             <div class="content1 px-0 mb-4 m-">
                                <div class="row">
                                    <div class="col-lg-3 col-md-3 col-sm-4 col-3 ">
                                        <div class="content_image">
                                            <img src={item.thumb} alt="inner" />                                     
                                        </div>    
                                    </div>
                                    <div class="col-sm-8 col-md-8 col-lg-8 col-8">
                            {/* The Big Chill  Cakery */}
                                        <p class="content_head "><Link to={`/rest/${item._id}`}>{item.name}</Link></p>
                                        <p class="content_subhead">{item.locality}</p>
                                        <p class="content_normal">{item.address}</p>
                                        <p class="review" id="star">Review--{item.review}
                                            <i class="fas fa-star fa1" id="stara" onClick={this.handleStar(1)} ></i>
                                            <i class="fas fa-star fa2" id="starb" ></i>
                                            <i class="fas fa-star fa3" id="starc" ></i>
                                            <i class="fas fa-star fa4" id="stard" ></i>
                                            <i class="fas fa-star fa5" id="stare" ></i>
                                        </p>
                                    </div>
                                </div>
                                <hr/>
                                <div class="content2 m-2">
                                    <div class="row">
                                        <div class="col-lg-3 col-md-3 col-sm-4 col-3">
                                            <div class="content_cuisine">
                                                <p>Cusines</p>
                                                <p>Cost For Two</p>                                    
                                            </div>
                                        </div>
                                        <div class="col-sm-8 col-md-8 col-lg-8 col-8">
                                            <div class="cuisinetype">
                                                <p>{item.Cuisine[0].name},{item.Cuisine[1].name}</p>
                                                <p>{item.cost}</p> 
                                            </div>
                                        </div>
                                    </div> 
                                </div>
                             </div>                 
                          </div>
                        </div>
                            )
                        })
                    }
            else{
                 return(
                     <div>
                         <center><h2>No data Avail!</h2></center>
                     </div>
                        )
                }  
            }
            else
            {
                return(
                    <div>
                        <img src="./images/Preloader.gif" alt=""/>
                    </div>
                )
            }
            
        }
   

        return(
        
            // boxes
                <div>        
                     {renderRest(this.props)}
                </div>      
            
            )
    
    }
   
}
export default ListingDisplay

