import React from 'react';
// import {Tab, Tabs, TabList, TabPanel} from 'react-tabs';
// import 'react-tabs/style/react-tabs.css';

const OrderView = (props) => {

    const renderTable =  ({orderdata}) => {
        if(orderdata){
            return orderdata.map((item) => {
                return(
                    <tr>
                        <td>{item._id}</td>
                        <td>{item.rest_id}</td>
                        <td>{item.name}</td>
                        <td>{item.phone}</td>
                        <td>{item.person}</td>
                        <td>Placed order</td>
                        
                        {/* <td>Placed</td> */}
                    </tr>
                      )
                })
            }
    }
    //  id="update" onLoad={ setTimeout(myFunction, 3000)}--use in order api
    function myFunction() {
        document.getElementById("update").innerHTML = "Order Placed";
      
    }
    return(
        <div className="d-block justify-content-center">
            <center><h3>Booking List</h3></center>
                <center>
                   <table className="table table-responsive ">
                    <thead>
                        <tr>
                            <th>Order Id</th>
                            <th>Rest Name</th>
                            <th>Name</th>
                            <th>Phone</th>
                            <th>Person</th>
                            <th>Status</th>
                        </tr>
                    </thead>
                    <tbody>
                        {renderTable(props)}
                    </tbody>
                   </table>
                </center>
            </div>
    )
}

export default OrderView;