import React, {Component} from 'react';
import axios from 'axios';
import {Link} from 'react-router-dom';
import {Tab, Tabs, TabList, TabPanel} from 'react-tabs';
import 'react-tabs/style/react-tabs.css';
import './RestaurantDetails.css'

const url = "https://developrestaurant.herokuapp.com/restaurantDetails";

class RestaurantDetails extends Component{
    constructor(props)
    {
        super(props)
        
        this.state={
            rest:{
                    "Cuisine":
                    [   {
                        "name":''
                        },   
                        {
                        "name":''
                        }
                    ]
                }
            }
    }
    onBack=()=>{
        let mealid=sessionStorage.getItem('type');
        this.props.history.push(`/list/${mealid}`)
    }
    render(){
        var {rest}=this.state;
        return(
            
            <div class="container mb-4">
                <div class="head_listing">{rest.name}</div>
                  <div className="row">
                    <div className="col-12">  
                        <div class="imagecontainer">
                            <img src={rest.thumb} alt="menu" class="img-fluid"/>
                            <button class="click">Click to see an Image</button>
                        </div>
                    </div>
                     <div className="col-12">
                        <div className="quickSearchContainer">
                            <p class="quickSearchHeading">
                                {rest.name}
                            <br/>
                                {rest.locality}
                                <br/>
                                {rest.address}
                            </p>
                        </div>
                     </div>
                    </div>
                    <br/>
                    <div>
                        <Tabs>
                            <TabList>
                                <Tab>Overview</Tab>
                                <Tab>Contact</Tab>
                            </TabList>
                            <TabPanel>
                                <div class="place">
                                    <p>About this {rest.name} Restaurant</p>
                                    <p class="one">Cuisine</p>
                                    <p class="two">{rest.Cuisine[0].name},{rest.Cuisine[1].name}</p>
                                        <p class="one">
                                            Average Cost
                                        </p>
                                        <p class="third"><span>&#8377;</span>{rest.cost} for two people(approx.)
                                        </p>
                                </div>
                            </TabPanel>
                            <TabPanel>
                            <div className="contact">
                                <p className="one"><i className="fas fa-contact"></i>Contact Number-</p>
                                <p className="two">{rest.contact_number}</p>      
                            <p className="one">Address-</p>
                            <p className="two">{rest.address}</p>
                            </div>
                            </TabPanel>
                        </Tabs>    
                    </div> 
                    <br/>
                    <br/>
                    <div>
                        <button className="btn btn-danger" onClick={this.onBack}>Back</button>
                        <Link className="btn btn-success"
                        to={`/order/${this.props.match.params.id}`}>Place Order</Link>
                    </div>   
                </div>
            )
    }
    componentDidMount(){
        let restid= this.props.match.params.id;
        axios.get(`${url}/${restid}`)
        .then((response)=>{
            this.setState({rest:response.data[0]})
        })
    }
}
 export default RestaurantDetails;
