import React, {Component} from 'react'
import axios from 'axios'
import './listing.css'
import ListingDisplay from './listingDisplay'
import CostFilter from '../filter/costFilter';
import CuisineFilter from '../filter/cuisineFilter';


const lurl="https://developrestaurant.herokuapp.com/restaurant?mealtype="



class Listing extends Component{
    constructor(){
        super()

        this.state={
            restListing:''
        }
    }
    setDataPerFilter(sortedData){
        this.setState({restListing:sortedData})
    }



    // star=(a)=>{
        
    // }
  
    render(){
        return(
            <div className="container ">
                <p class="Head mb-5">Restaurants in your City</p>
                <div class="box mb-3 d-lg-none d-block">
                     <a class="nav" data-toggle="collapse" href="#collapseExample" role="button" aria-expanded="false" aria-controls="collapseExample" >
                         <span class="glyphicon glyphicon-chevron-down"></span>
                    </a>
                </div>

                <div class="row">
                    <div class="col-sm-0 col-md-0 col-lg-3">
                        <div class="sort d-lg-block collapse" id="collapseExample">
                            <p class="filter_head">Filters</p>
                            <div class="cuisine mb-2">
                                <CuisineFilter datapercuisine={(data)=>{this.setDataPerFilter(data)}}/>
                            </div>
                            <div class="Costfortwo mb-2">
                                <CostFilter datapercost={(data)=>{this.setDataPerFilter(data)}}/>
                            </div>
                            {/* <div class="high-low"> */}
                                {/* <p class="Sort_head">Sort</p> */}
                            {/* </div> */}
                        </div>                   
                    </div>
                    <div class="col-sm-0 col-md-0 col-lg-1 px-0"></div>
                    <div class="col-sm-12 col-md-12 col-lg-8">
                        <ListingDisplay restdata={this.state.restListing} />           
                    </div>
                </div>
            </div>    
        
            )   
         }

    componentDidMount(){
        var mealId= this.props.match.params.id;
        sessionStorage.setItem('type',mealId)
        axios.get(`${lurl}${mealId}`)
        .then((response)=>{
            this.setState({restListing:response.data})
        })
    }
}

export default Listing